import { reactive, toRefs, onMounted } from 'vue';
import { useRouter } from 'vue-router';
export default {
  name: 'LoadIng',

  setup() {
    const router = useRouter();
    let loadTheFirstScreen = reactive({
      loadingText: 'Loading...',
      loadingTextShow: true
    });
    let welcome = reactive({
      avatarDisPlay: false,
      titleDisPlay: false,
      welcomeDisPlay: false,
      goToWeb: false,
      onClickToJump: false,
      loadIsLeave: true
    });

    let progress = function () {
      setTimeout(() => {
        loadTheFirstScreen.loadingTextShow = false;
        setTimeout(() => {
          welcome.avatarDisPlay = true;
          setTimeout(() => {
            welcome.titleDisPlay = true;
            setTimeout(() => {
              welcome.welcomeDisPlay = true;
              setTimeout(() => {
                welcome.goToWeb = true;
                setTimeout(() => {
                  welcome.onClickToJump = true;
                }, 1000);
              }, 250);
            }, 250);
          }, 250);
        }, 1000);
      }, 2200);
    };

    onMounted(() => {
      progress();
      preLoadedResources();
    });

    const goInHome = function () {
      router.replace('home');
    };

    const preLoadedResources = function () {
      // 图片
      let imgs = [require('@/assets/AE.png'), require('@/assets/AIMP.png'), require('@/assets/Avatar.jpg'), require('@/assets/CalendarBackground.jpg'), require('@/assets/CalendarBackground.jpg'), require('@/assets/ccleane.png'), require('@/assets/CommonWebsiteHeadFigure.jpg'), require('@/assets/CPU-Z.png'), require('@/assets/DG.png'), require('@/assets/DG2.png'), require('@/assets/DLL.png'), require('@/assets/DLR.png'), require('@/assets/DryingTutorialHeadFigure.jpg'), require('@/assets/everything.png'), require('@/assets/huoron.png'), require('@/assets/music-one.png'), require('@/assets/music-one.svg'), require('@/assets/OBS.png'), require('@/assets/playerBackground.jpg'), require('@/assets/PS.png'), require('@/assets/SoftwareRecommendationHeadFigure.jpg'), require('@/assets/tasker.png'), require('@/assets/VSC.png'), require('@/assets/WebFromEndHeadFigure.jpg')];

      for (let img of imgs) {
        let image = new Image();
        image.src = img;
      }
    };

    return { ...toRefs(loadTheFirstScreen),
      ...toRefs(welcome),
      goInHome
    };
  }

};