import { getCurrentInstance } from 'vue';
import { reactive, ref, toRefs } from '@vue/reactivity';
import { GoStart, Pause, PlayOne, GoEnd, LeftOne, RightOne } from '@icon-park/vue-next';
import { onBeforeUnmount, onMounted, watch } from '@vue/runtime-core';
import axios from 'axios'; // 导入节流函数

import stopUpDownFunList from '@/hooks/stopUpDownFunList.js';
import stopUpDownFunList2 from '@/hooks/stopUpDownFunList2.js';
import { useRouter } from 'vue-router';
export default {
  name: 'IndexMusic',
  components: {
    GoStart,
    Pause,
    PlayOne,
    GoEnd,
    LeftOne,
    RightOne
  },

  setup() {
    // 声明路由
    const $router = useRouter(); // 进度条DOM

    let musicBar = ref(); // 节流函数

    const stopUpDown = stopUpDownFunList();
    const stopUpDown2 = stopUpDownFunList2(); // 播放状态

    let playPause = ref(false);
    let hiddenMini = ref(false);
    let timer = null;
    let audioDom = '';
    let NeteaseCloudMusicPlayer = reactive({
      Cover: require('@/assets/music-one.svg'),
      overallBackground: require('@/assets/playerBackground.jpg'),
      title: [{}],
      showPlayOne: false,
      showPause: true,
      // 自定义接口
      // 歌单ID(请确保歌单内的歌曲都是可以在网易云内在线播放并且不需要VIP)
      songListId: '7500267885',
      // 当前歌曲名
      nameOfTheSong: '暂无歌曲',
      // 当前歌手名
      singerName: '暂无歌手',
      // 当前歌曲地址
      songAddress: '',
      // 第几首
      numberOfSongs: 0,
      // 进度条的值
      musicBarValue: 0,
      // 总时长
      durationOfMusic: 0,
      // 有效歌曲数量
      effectiveSong: 0,
      // 封面背景
      coverBackground: {
        backgroundImage: 'url()',
        backgroundSize: 'cover',
        backgroundPositionX: '0%',
        backgroundPositionY: '30%',
        backgroundRepeat: 'no-repeat'
      }
    }); // 吸顶模式

    let topMode = ref(false); // 最小化

    let minimize = ref(false); // 整体显示

    let allDisplay = ref(false);

    const start = function () {
      audioDom.refs.audio.play();
      audioTime();
      playPause.value = true;
    };

    const pause = function () {
      audioDom.refs.audio.pause();
      clearInterval(timer);
      timer = null;
      playPause.value = false;
    };

    const playClick = function () {
      stopUpDown2(() => {
        playPause.value = !playPause.value;
      });
    };

    const refresh = function () {
      NeteaseCloudMusicPlayer.nameOfTheSong = NeteaseCloudMusicPlayer.title[NeteaseCloudMusicPlayer.numberOfSongs].name;
      NeteaseCloudMusicPlayer.singerName = NeteaseCloudMusicPlayer.title[NeteaseCloudMusicPlayer.numberOfSongs].ar[0].name;
      NeteaseCloudMusicPlayer.songAddress = `https://music.163.com/song/media/outer/url?id=${NeteaseCloudMusicPlayer.title[NeteaseCloudMusicPlayer.numberOfSongs].id}.mp3`;
      NeteaseCloudMusicPlayer.Cover = NeteaseCloudMusicPlayer.title[NeteaseCloudMusicPlayer.numberOfSongs].al.picUrl;
      NeteaseCloudMusicPlayer.overallBackground = NeteaseCloudMusicPlayer.Cover;

      if (topMode.value == false) {
        NeteaseCloudMusicPlayer.coverBackground.backgroundImage = `url(${NeteaseCloudMusicPlayer.Cover})`;
      }
    }; // 获取当前音频时长


    const audioTime = function () {
      clearInterval(timer);
      NeteaseCloudMusicPlayer.durationOfMusic = audioDom.refs.audio.duration;
      timer = setInterval(() => {
        if (isNaN(NeteaseCloudMusicPlayer.durationOfMusic)) {
          NeteaseCloudMusicPlayer.durationOfMusic = audioDom.refs.audio.duration;
        }

        NeteaseCloudMusicPlayer.musicBarValue = audioDom.refs.audio.currentTime;

        if (NeteaseCloudMusicPlayer.musicBarValue >= audioDom.refs.audio.duration) {
          clearInterval(timer);
          nextSong();
        }
      }, 250);
    }; // 控制播放开关


    watch(playPause, newValue => {
      refresh();

      if (newValue === false) {
        NeteaseCloudMusicPlayer.showPause = true;
        NeteaseCloudMusicPlayer.showPlayOne = false;
        setTimeout(() => {
          pause();
        }, 200);
      } else {
        NeteaseCloudMusicPlayer.showPause = false;
        NeteaseCloudMusicPlayer.showPlayOne = true;
        setTimeout(() => {
          start();
        }, 200);
      }
    });
    watch(topMode, () => {
      if (topMode.value == true) {
        NeteaseCloudMusicPlayer.coverBackground = {
          backgroundImage: 'url()',
          backgroundSize: 'cover',
          backgroundPositionX: '0%',
          backgroundPositionY: '30%',
          backgroundRepeat: 'no-repeat'
        };
      } else {
        NeteaseCloudMusicPlayer.coverBackground = {
          backgroundImage: `url(${NeteaseCloudMusicPlayer.overallBackground})`,
          backgroundSize: 'cover',
          backgroundPositionX: '0%',
          backgroundPositionY: '30%',
          backgroundRepeat: 'no-repeat'
        };
      }
    }); // 下一曲

    const nextSong = function () {
      stopUpDown(() => {
        NeteaseCloudMusicPlayer.numberOfSongs++;
        playPause.value = true;

        if (NeteaseCloudMusicPlayer.numberOfSongs > NeteaseCloudMusicPlayer.effectiveSong) {
          NeteaseCloudMusicPlayer.numberOfSongs = 0;
          refresh();
          setTimeout(() => {
            start();
          }, 200);
        } else {
          refresh();
          setTimeout(() => {
            start();
          }, 200);
        }
      });
    }; // 上一首


    const previousSong = function () {
      stopUpDown(() => {
        NeteaseCloudMusicPlayer.numberOfSongs -= 1;
        playPause.value = true;

        if (NeteaseCloudMusicPlayer.numberOfSongs < 0) {
          NeteaseCloudMusicPlayer.numberOfSongs = NeteaseCloudMusicPlayer.effectiveSong;
          refresh();
          setTimeout(() => {
            start();
          }, 200);
        } else {
          refresh();
          setTimeout(() => {
            start();
          }, 200);
        }
      });
    }; // 获取播放列表


    const getSongList = function () {
      axios.get(`https://netease-cloud-music-api-two-ochre-57.vercel.app/playlist/track/all?id=${NeteaseCloudMusicPlayer.songListId}&limit=0&offset=0`).then(({
        data
      }) => {
        NeteaseCloudMusicPlayer.title = data.songs;
        NeteaseCloudMusicPlayer.effectiveSong = NeteaseCloudMusicPlayer.title.length - 1;
      }).catch(console.error);
    }; // 进度条点击逻辑
    //鼠标按下时


    const surgeryNoteClickDown = function () {
      clearInterval(timer);
      timer = null;
    }; // 鼠标松开时


    const surgeryNoteClickUp = function () {
      audioDom.refs.audio.currentTime = NeteaseCloudMusicPlayer.musicBarValue;
      start();
    };

    onMounted(() => {
      audioDom = getCurrentInstance();
      getSongList();

      if (topMode.value == false) {
        NeteaseCloudMusicPlayer.coverBackground.backgroundImage = `url(${NeteaseCloudMusicPlayer.overallBackground})`;
      }
    });
    onBeforeUnmount(() => {
      clearInterval(timer);
      timer = null;
    }); // 监听路由

    watch(() => $router.currentRoute.value.path, (newValue, oldValue) => {
      if (newValue == '/home' && oldValue !== '/home') {
        allDisplay.value = false;
        hiddenMini.value = false;
        setTimeout(() => {
          topMode.value = false;
          minimize.value = false;
          allDisplay.value = true;
        }, 500);
      } else if (newValue !== '/home' && oldValue == '/home') {
        allDisplay.value = false;
        hiddenMini.value = true;
        setTimeout(() => {
          topMode.value = true;
          allDisplay.value = true;
        }, 500);
        setTimeout(() => {
          minimize.value = true;
        }, 1000);
      } else if (newValue == '/loading...') {
        allDisplay.value = false;
      } else if (newValue !== '/home' && oldValue == '/') {
        allDisplay.value = false;
        hiddenMini.value = true;
        setTimeout(() => {
          topMode.value = true;
          allDisplay.value = true;
        }, 500);
        setTimeout(() => {
          minimize.value = true;
        }, 1000);
      }
    }, {
      immediate: true
    });
    return { ...toRefs(NeteaseCloudMusicPlayer),
      playPause,
      nextSong,
      previousSong,
      musicBar,
      surgeryNoteClickDown,
      surgeryNoteClickUp,
      topMode,
      minimize,
      allDisplay,
      hiddenMini,
      playClick
    };
  }

};