import { PrismEditor } from 'vue-prism-editor';
import 'vue-prism-editor/dist/prismeditor.min.css'; // import the styles somewhere

import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-javascript';
import 'prismjs/themes/prism-tomorrow.css'; // import syntax highlighting styles

import { reactive, ref, toRefs } from '@vue/reactivity';
import { onMounted } from '@vue/runtime-core';
import { DoubleLeft, LinkOne } from '@icon-park/vue-next';
import { useRouter } from 'vue-router';
export default {
  name: 'webFromEndText',
  components: {
    DoubleLeft,
    LinkOne,
    PrismEditor
  },

  setup() {
    const $router = useRouter();
    let shows = ref(false);
    let CodeData = reactive({
      code1: "<template>\n  <div class=\"train-city\">\n    <h3>父组件传给子组件的toCity:{{sendData}}</h3>\n    <br/><button @click='select(`大连`)'>点击此处将‘大连’发射给父组件</button>\n  </div>\n</template>\n<script>\n  export default {\n    name:'trainCity',\n    props:['sendData'], // 用来接收父组件传给子组件的数据\n    methods:{\n      select(val) {\n        let data = {\n          cityname: val\n        };\n        this.$emit('showCityName',data);//select事件触发后，自动触发showCityName事件\n      }\n    }\n  }\n<\/script>",
      code2: '<template>\n    <div>\n        <div>父组件的toCity{{toCity}}</div>\n        <train-city @showCityName="updateCity" :sendData="toCity"></train-city>\n    </div>\n<template>\n<script>\n  import TrainCity from "./train-city";\n  export default {\n    name:\'index\',\n    components: {TrainCity},\n    data () {\n      return {\n        toCity:"北京"\n      }\n    },\n    methods:{\n      updateCity(data){//触发子组件城市选择-选择城市的事件\n        this.toCity = data.cityname;//改变了父组件的值\n        console.log(\'toCity:\'+this.toCity)\n      }\n    }\n  }\n<\/script>',
      code3: '<template>\n  <div class="train-city">\n    <h3>父组件传给子组件的toCity:{{sendData}}</h3> \n    <br/><button @click=\'select(`大连`)\'>点击此处将‘大连’发射给父组件</button>\n  </div>\n</template>\n\n<script>\nexport default {\n  name: "train-city",\n  props:{\n    sendData:{\n      Type:String,\n      default:""\n    }\n  },\n  emits: ["showCityName"],\n  setup(props,{emit}) {\n    return {\n      select: () => { emit(\'showCityName\') }\n    }\n  },\n}\n<\/script>',
      code4: '<template>\n    <div>\n        <div>父组件的toCity{{toCity}}</div>\n        <train-city @showCityName="updateCity" :sendData="toCity"></train-city>\n    </div>\n<template>\n<script>\nimport TrainCity from "./train-city";\nimport { reactive, toRefs} from \'@vue\';\nexport default {\n  name: "parent-com",\n  components: { TrainCity },\n  setup() {\n    const state = reactive({ toCity:"北京" })\n\n    const updateCity = (data) => {\n         state.toCity = data.cityname;//改变了父组件的值\n         console.log(\'toCity:\' + state.toCity)\n    }\n\n    return {\n      ...toRefs(state ),\n      updateCity\n    }\n  },\n}\n<\/script>'
    });
    let inCodeData = reactive({});

    const backToTheUpperLevel = function () {
      shows.value = false;
      setTimeout(() => {
        $router.push({
          name: 'WebFromEnd'
        });
      }, 1000);
    };

    onMounted(() => {
      shows.value = true;
    });
    return { ...toRefs(CodeData),
      ...toRefs(inCodeData),
      shows,
      backToTheUpperLevel
    };
  },

  methods: {
    highlighter(code) {
      return highlight(code, languages.js);
    }

  }
};